@import "../../assets/styles/variables.scss";
.page-catalogue {
	padding: 20px 0;
	@media (max-width: 62em) {
		padding: 20px 0 30px;
	}
	// .page-catalogue__inner
	&__inner {
	}
	// .page-catalogue__title
	&__title {
		margin-bottom: 0.625em;
	}
	// .page-catalogue__filters
	&__filters {
		margin-bottom: 1.5em;
	}
	// .page-catalogue__products
	&__products {
	}
}
.products-catalogue {
	// .products-catalogue__empty
	&__empty {
		& + .products-catalogue__list {
			padding: 0;
		}
	}
	// .products-catalogue__list
	&__list {
		padding: 30px 0;
	}
}
