@import "../../assets/styles/variables.scss";
.catalogue-pagination {
	.MuiPagination-ul {
		justify-content: center;
	}
	&__item {
		&.MuiPaginationItem-root {
			min-width: 3rem;
			height: 3rem;
			border-radius: 50%;
			font-size: 1.25rem;
			line-height: 1;
			border: 1px solid $accentColor;
			&.Mui-selected {
				background-color: $accentColor;
				&:hover {
					background-color: $accentColor;
				}
			}
			&.Mui-focusVisible {
				outline: 2px solid $blue1Color;
				outline-offset: 2px;
				background-color: $accentHoverColor;
			}
			&:hover {
				background-color: $accentHoverColor;
			}
			@media (max-width: 30em) {
				font-size: 1rem;
				min-width: 2.5rem;
				height: 2.5rem;
			}
		}
		&.MuiPaginationItem-ellipsis {
			display: flex;
			align-items: center;
			justify-content: center;
			pointer-events: none;
		}
		.MuiTouchRipple-root {
			display: none;
		}
	}
}
