@import "../../assets/styles/variables.scss";
.filters-catalogue {
	// .filters-catalogue__list
	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 0.5rem 1.25rem;
		flex-wrap: wrap;
		@media (max-width: 62em) {
			gap: 0.25rem 0.75rem;
		}
		@media (max-width: 30em) {
			gap: 0.5rem;
		}
	}
	// .filters-catalogue__item
	&__item {
	}
	// .filters-catalogue__label
	&__label {
		& > span {
			position: relative;
			z-index: 1;
			display: inline-block;
			padding: 0.5em 1em;
			border-radius: 1.34em;
			background-color: $hoverBackgroundColor;
			font-size: 1.5rem;
			color: $mainDarkColor;
			cursor: pointer;
			transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
			@media (any-hover: hover) {
				&:hover {
					background-color: $accentHoverColor;
				}
			}
			&:active {
				color: $buttonActiveColor;
				transition: none;
			}
			@media (max-width: 62em) {
				font-size: 1.25rem;
			}
			@media (max-width: 30em) {
				font-size: 1rem;
			}
		}
	}
	// .filters-catalogue__input
	&__input {
		&:focus-visible + span {
			outline: 2px solid $blue1Color;
			outline-offset: 2px;
		}
	}
	// .filters-catalogue__item-bg
	&__item-bg {
		position: absolute;
		z-index: 2;
		inset: 0;
		display: block;
		background-color: $accentColor;
		border-radius: 2rem;
	}
	// .filters-catalogue__item-title
	&__item-title {
		position: relative;
		z-index: 3;
	}
}
