.catalogue-list {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(23.5rem, 1fr));
	gap: 3.75rem 2.5rem;
	list-style: none;
	@media (max-width: 62em) {
		gap: 2.5rem 1.5rem;
	}
	@media (max-width: 30em) {
		grid-template-columns: auto;
	}
	// .catalogue-list__item
	&__item {
		.product-card {
			max-width: unset;
			@media (max-width: 30em) {
				max-width: 20rem;
				margin: 0 auto;
			}
		}
		.product-card__title {
			min-height: 2.5em;
			@media (max-width: 48em) {
				min-height: unset;
			}
		}
		.user-actions {
			margin: 0 auto;
		}
		.add-to-cart-button {
			@media (max-width: 62em) {
				font-size: 1.25rem;
				border-radius: 1.6em;
			}
		}
	}
}
